import { InitPageBase } from '../crawl/InitPageBase'
import { Colors } from '../Colors'
import { lang } from '../Language'
import { Tools } from '../Tools'
import { filter, FilterOption } from '../filter/Filter'
import { store } from '../store/Store'
import { log } from '../Log'
import { pageType } from '../PageType'
import { settings } from '../setting/Settings'
import { API } from '../API'
import { Config } from '../Config'
import { NovelItem } from '../crawl/CrawlResult.d'
import { nameRuleManager } from '../setting/NameRuleManager'
import { Utils } from '../utils/Utils'

// 
// Pixiv 
//  API 404
class InitRankingNovelPageNew extends InitPageBase {
  constructor() {
    super()
    this.init()
  }

  protected addCrawlBtns() {
    Tools.addBtn(
      'crawlBtns',
      Colors.bgBlue,
      '_',
      '_Title',
      'crawlRankingWork'
    ).addEventListener('click', () => {
      this.readyCrawl()
    })
  }

  protected initAny() {
    Tools.hiddenPremiumAD()
  }

  // 
  protected sortResult() {
    //  {rank} rank 
    if (nameRuleManager.rule.includes('{rank}')) {
      store.result.sort(Utils.sortByProperty('rank', 'asc'))
      store.resultMeta.sort(Utils.sortByProperty('rank', 'asc'))
    }
  }

  protected getWantPage() {
    // 
    this.crawlNumber = settings.crawlNumber[pageType.type].value
    if (this.crawlNumber === -1) {
      log.warning(lang.transl('_'))
    } else {
      log.warning(
        lang.transl('_x', this.crawlNumber.toString())
      )
    }
    //  -1
    if (this.crawlNumber === -1) {
      this.crawlNumber = this.maxCount
    }
  }

  //  3  R-18 
  // 
  //  5  R-18 
  // AI
  //  1  R-18G 
  // 
  //  14 8 5  R-181  R-18G
  private mode = 'daily'
  private date: string | null = null
  private page = 1
  /**
   *  API  API 
   * 
   */
  private selectLang = ''

  /** */
  private checkTotal = 0

  protected nextStep() {
    this.getParams()
    this.getIdList()
  }

  private getParams() {
    //  url 
    // https://www.pixiv.net/novel/ranking.php?mode=weekly_r18&p=2&date=20251031
    const url = new URL(window.location.href)

    // 
    //  mode 
    this.mode = url.searchParams.get('mode') || 'daily'

    // 
    //  pixiv 
    this.date = url.searchParams.get('date')

    // 
    const p = url.searchParams.get('p')
    if (p) {
      this.page = Number.parseInt(p!)
    } else {
      //  p  1 
      this.page = 1
    }
    //  100  50  2 
    // AI 50  1 

    // 
    // pixiv  ''
    const value = window.localStorage.getItem('rankinglanguageFilterSetting')
    if (value !== null) {
      this.selectLang = value
    }
  }

  protected async getIdList() {
    try {
      const json = await API.getRankingDataNovel(
        this.mode,
        this.date,
        this.page
      )

      this.listPageFinished++

      log.log(
        lang.transl('_', this.listPageFinished.toString()),
        1,
        false
      )

      const display_a = json.body.display_a
      let list = display_a.rank_a

      // list 
      //  50  length  50
      const length = 50
      for (let i = 0; i < length; i++) {
        const novel: NovelItem = (list as any)[i]

        // 
        // 2  100  99 
        if (novel === undefined) {
          continue
        }

        const filterOpt: FilterOption = {
          id: novel.id,
          workType: 3,
          tags: novel.tag_a,
          bookmarkCount: novel.bookmark_count,
          bookmarkData: novel.is_bookmarked,
          userId: novel.user_id,
        }

        let checkLang = true
        if (this.selectLang) {
          checkLang = novel.language === this.selectLang
        }
        if (!checkLang) {
          log.warning(
            lang.transl(
              '_',
              this.selectLang
            ),
            1,
            false,
            'excludeNovelByUserSelectLanguage'
          )
        }

        if ((await filter.check(filterOpt)) && checkLang) {
          const id = novel.id.toString()
          store.setRankList(id, Number.parseInt(novel.rank as string))
          store.idList.push({
            type: 'novels',
            id: id,
          })
        }

        this.checkTotal++
        if (this.checkTotal >= this.crawlNumber) {
          return this.getIdListFinished()
        }
      }

      // 
      if (store.idList.length >= this.crawlNumber || !display_a.next) {
        this.getIdListFinished()
      } else {
        // 
        this.page = display_a.next
        this.getIdList()
      }
    } catch (error: Error | any) {
      if (error.status === 404) {
        // 404
        console.log('404')
        this.getIdListFinished()
      }

      // 429 
      if (error.status === 429) {
        this.log429ErrorTip()
        window.setTimeout(() => {
          this.getIdList()
        }, Config.retryTime)
      }

      return
    }
  }

  protected resetGetIdListStatus() {
    this.checkTotal = 0
    this.listPageFinished = 0
  }
}
export { InitRankingNovelPageNew }
